@import "https://fonts.googleapis.com/css?family=Montserrat:400,700,900&display=swap";
:root {
  --gradient: linear-gradient(135deg, #72edf2 10%, #5151e5 100%);
}

* {
  box-sizing: border-box;
  line-height: 1.25em;
}

.clear {
  clear: both;
}

body {
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(to right, #000, #434343);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: Montserrat, sans-serif;
  display: flex;
}

.container {
  color: #fff;
  height: 400px;
  background-color: #222831;
  border-radius: 25px;
  box-shadow: 0 0 70px -10px #0003;
}

.weather-side {
  height: 100%;
  width: 300px;
  float: left;
  background-image: url("https://lh3.googleusercontent.com/tT6_cBTn-TdzlUHP0O02mO3NGVjIgyJDXL4gBOStp0unlsHviSWvzKzz85UARf4AiHPLUzJ8dd_XDgPHXgAkCI4w8XYwUWCXLKXqtUT5");
  border-radius: 25px;
  transition: transform .3s;
  position: relative;
  transform: translateZ(0)scale(1.02)perspective(1000px);
  box-shadow: 0 0 20px -10px #0003;
}

.weather-side:hover {
  transform: scale(1.1)perspective(1500px)rotateY(10deg);
}

.weather-gradient {
  width: 100%;
  height: 100%;
  background-image: var(--gradient);
  opacity: .8;
  border-radius: 25px;
  position: absolute;
  top: 0;
  left: 0;
}

.date-container {
  position: absolute;
  top: 25px;
  left: 25px;
}

.date-dayname {
  margin: 0;
}

.date-day {
  display: block;
}

.location {
  margin-top: 10px;
  display: inline-block;
}

.location-icon {
  height: .8em;
  width: auto;
  margin-right: 5px;
  display: inline-block;
}

.weather-container {
  position: absolute;
  bottom: 25px;
  left: 25px;
}

.weather-icon.feather {
  height: 60px;
  width: auto;
}

.weather-temp {
  margin: 0;
  font-size: 4em;
  font-weight: 700;
}

.weather-desc {
  margin: 0;
}

.info-side {
  float: left;
  height: 100%;
  padding-top: 25px;
  position: relative;
}

.today-info {
  border-radius: 10px;
  margin: 0 25px 25px;
  padding: 15px;
}

.today-info > div:not(:last-child) {
  margin: 0 0 10px;
}

.today-info > div .title {
  float: left;
  font-weight: 700;
}

.today-info > div .value {
  float: right;
}

.week-list {
  border-radius: 10px;
  margin: 10px 35px;
  padding: 0;
  list-style-type: none;
  box-shadow: 0 0 50px -5px #00000040;
}

.week-list > li {
  float: left;
  cursor: pointer;
  border-radius: 10px;
  padding: 15px;
  transition: all .2s;
}

.week-list > li:hover {
  color: #222831;
  background: #fff;
  transform: scale(1.1);
  box-shadow: 0 0 40px -5px #0003;
}

.week-list > li.active {
  color: #222831;
  background: #fff;
  border-radius: 10px;
}

.week-list > li .day-name {
  text-align: center;
  margin: 10px 0 0;
  display: block;
}

.week-list > li .day-icon {
  height: 30px;
  width: auto;
  margin: 0 auto;
  display: block;
}

.week-list > li .day-temp {
  text-align: center;
  margin: 10px 0 0;
  font-weight: 700;
  display: block;
}

.location-container {
  padding: 25px 35px;
}

.location-button {
  width: 100%;
  box-sizing: border-box;
  background-image: var(--gradient);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 25px;
  outline: none;
  padding: 10px;
  font-family: Montserrat, sans-serif;
  font-weight: 700;
  transition: transform .2s;
  box-shadow: 0 0 30px -5px #00000040;
}

.location-button:hover {
  transform: scale(.95);
}

.location-button .feather {
  height: 1em;
  width: auto;
  margin-right: 5px;
}

.weather__icon {
  transform: scale(1.5);
}

.weather__icon--small {
  margin: -1rem;
  transform: scale(.8);
}

.input__box {
  width: 520px;
  height: 50px;
  color: #9d9d9d;
  background-color: #222831;
  border: none;
  border-radius: 30px;
  margin-bottom: 20px;
  padding: 20px;
  font-size: 20px;
}

.input__box:focus {
  outline: 1px solid #c3c3c3;
  transition: all .3s;
  transform: scale(1.1);
}

.input__container {
  width: 670px;
  height: 50px;
  background-color: #222831;
  border: none;
  border-radius: 30px;
  margin-bottom: 20px;
}

.input__submit {
  width: 115px;
  height: 50px;
  background-image: var(--gradient);
  color: #fff;
  border: none;
  border-radius: 30px;
  outline: none;
  margin-bottom: 20px;
  margin-left: 30px;
  padding: 0 20px;
  font-size: 20px;
  transition: all .3s;
}

.input__submit::placeholder {
  color: #fff;
}

.input__submit:hover {
  transform: scale(.95);
}

.scale-up {
  transition: all .5s;
  transform: scale(1.1);
}

